.team-page-container {
    background: linear-gradient(
        109.6deg,
        rgb(1, 24, 54) 11.2%,
        rgba(0, 37, 84, 0.8) 100.2%
    );
    padding: 60px 15px;
    min-height: 100vh;
}

.banner-card-row {
    background: #ffffff;
    padding: 40px;
    border-radius: 8px;
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    transition: all 0.5s ease;
    /* Keeps cards centered and prevents edge-to-edge look */
    margin-left: auto;
    margin-right: auto;
    max-width: 1000px;
    width: 100%;
}

.banner-card-row:hover {
    background: #28a745 !important;
    transform: translateY(-5px);
}

.banner-card-row:hover .member-name,
.banner-card-row:hover .member-role,
.banner-card-row:hover .member-bio,
.banner-card-row:hover .social-links i {
    color: #ffffff !important;
}

.banner-img-frame {
    display: inline-block;
    padding: 10px;
    border: 2px solid #28a745;
    border-radius: 8px;
    width: 240px;
    height: 240px;
    overflow: hidden;
    background: #f8f9fa;
    transition: border-color 0.5s ease;
}

.banner-card-row:hover .banner-img-frame {
    border-color: #ffffff;
}

.banner-img-frame img {
    width: 100% !important;
    height: 100% !important;
    object-fit: cover;
    border-radius: 4px;
}

.member-bio {
    color: #333333;
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    transition: all 0.5s ease;
}

.banner-card-row:hover .member-bio {
    opacity: 1;
    max-height: 200px;
}

.member-name {
    color: #000000;
    font-weight: bold;
    transition: color 0.5s ease;
}

.member-role {
    color: #666666;
    transition: color 0.5s ease;
}

.social-links i {
    color: #000000;
    transition: color 0.5s ease;
}
