/* Section Background */
.about-full-section {
    background: linear-gradient(
        109.6deg,
        rgb(1, 24, 54) 11.2%,
        rgba(0, 37, 84, 0.8) 100.2%
    );
    min-height: 75vh;
    color: #ffffff;
}

/* Image Frame */
.about-img-frame {
    display: inline-block;
    padding: 10px;
    border: 2px solid #28a745;
    border-radius: 8px;
    background: rgba(255, 255, 255, 0.05);
}

.about-img-frame img {
    max-height: 400px;
    object-fit: cover;
}

/* Typography */
.about-title {
    color: #ffffff;
    font-weight: 700;
}
.highlight-text {
    color: #28a745;
}
.about-subtitle {
    color: #28a745;
    letter-spacing: 2px;
}
.about-text-lead {
    color: #e0e0e0;
    font-size: 1.25rem;
    font-weight: 500;
}
.about-text-body {
    color: #cccccc;
    line-height: 1.8;
    font-size: 1.1rem;
}

/* Expansion Logic */
.expanded-content {
    max-height: 0;
    opacity: 0;
    overflow: hidden;
    transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.expanded-content.show {
    max-height: 2500px; /* Large enough for vertical content */
    opacity: 1;
    margin-top: 40px;
}

/* Vertical Content Box */
.content-inner {
    background: rgba(255, 255, 255, 0.03);
    border-radius: 20px;
    border: 1px solid rgba(255, 255, 255, 0.1);
}

.text-success {
    color: #28a745 !important;
    font-size: 2rem;
    font-weight: 700;
}

.border-secondary {
    border-color: rgba(255, 255, 255, 0.1) !important;
}

.about-btn {
    border-color: #28a745;
    color: #ffffff;
    font-weight: 600;
}

.about-btn:hover {
    background-color: #28a745;
    color: #ffffff;
}
